

















































































	import {
		mapState,
		mapMutations
	} from 'vuex';
	export default {
		computed: mapState([ 'hasLogin','userInfo']),
		data() {
			return {
				id:0,
				task:[],
			};
		},
		onLoad:function(e){
			uni.request({
				url:`${this.$serverUrl}/task/task_detail?id=`+e.id,
				header:{
					"Content-Type": "application/x-www-form-urlencoded",
					"token":this.userInfo.token
				},
			
				success:(res)=>{
					this.task = res.data;
				}
			})
		},	
}
	
